import React from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import axios from "axios";

export default class AssignMemberDialog extends React.Component {

  state = {
    members:[]
  }


  componentDidUpdate(prevProps){
    if (this.props.open === true) {
      axios.get('http://localhost:7900/api/get-unassigned-members',{
        params: {
          id: this.props.projectId
        }
      })
      .then(
        (response) =>{
          this.setState({ members: response.data , chosenMembers: Array(response.data.length).fill(false) });
          console.log(members);
        } 
       )
       .catch(err=> console.log(err));
    }
   
  }


  render() {
    return (
      <Dialog
        open={this.props.open}
        onClose={this.props.handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Assign New Member</DialogTitle>
        {/* <DialogContent> */}
        {/* {this.state.members.map(n => {
              return (
                n.member_name
              );
            })} */}
        {
          <List dense >
          {this.state.members.map((member,index) => (
            <ListItem key={member.id}>
              <ListItemText primary={member.member_name} />
              <ListItemSecondaryAction>
                <Checkbox
                  onChange={this.handleToggle(member.id)}
                  checked={this.state.chosenMembers[index]}
                />
              </ListItemSecondaryAction>
            </ListItem>
          ))}
        </List>
        }      
        {/* </DialogContent> */}
        <DialogActions>
          <Button onClick={this.props.handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={this.props.handleClose} color="primary">
            Add Member
          </Button>
        </DialogActions>
      </Dialog>
    );
  }
}
